<template>
  <div class="main-wrapper">
    <section>
      <div>
        <h1>ËæìÂÖ•‰ªªÊÑèÂ≠óÁ¨¶</h1>
      </div>
    </section>
    <section>
      <el-container class="main-wrapper-inner">
        <el-main>
          <!-- ‰∏ãÈù¢ÊòØ‰∏Ä‰∏™ÊñáÊú¨Ê°Ü -->
          <textarea
            name="input"
            id="input"
            v-model="inputKeywords"
            rows="10"
            placeholder="ËØ∑ËæìÂÖ•ÂÖ≥ÈîÆÂ≠ó"
          ></textarea>
          <!-- ‰∏ãÈù¢Â±ïÁ§∫ËøîÂõûÁöÑÊï∞ÊçÆ -->
          <div v-show="showReturn" class="show-box">
            <div></div>
            <el-space v-loading="showLoading" fill="fill" direction="vertical" style="width:100%;">
              <el-row class="translation-add">
                <el-col :span="24">
                  <el-card shadow="never"> <div class="translation-add-row"><span class="translation-word-title">{{inputKeywords}}</span><span class="translation-icon" @click="submitTranslation(inputKeywords)">‚ûï</span></div> </el-card>
                </el-col>
              </el-row>
              <el-row 
              v-for="word in inputData.data"
              :key="word"
              :gutter="3"
              class="single-translation">
                <el-col :span="word.id?16:24" :xm="word.id?16:24" :sm="word.id?18:24" :md="word.id?18:24" :lg="word.id?18:24" :xl="word.id?18:24">
                  <el-card class="translation-content" shadow="hover"> {{word.translation}} </el-card>
                </el-col>
                <el-col :span="4" :xm="4" :sm="3" :md="3" :lg="3" :xl="3" v-if="word.id">
                  <el-card class="translation-icon add-likes" shadow="hover" :data-translation="word.translation" @click="addTransLikes"> <div class="icon-wrapper"><span class="add-like-icon">üëç</span><span>{{word.likes}}</span></div> </el-card>
                </el-col>
                <el-col :span="4" :xm="4" :sm="3" :md="3" :lg="3" :xl="3" v-if="word.id">
                  <el-card class="translation-icon add-against" shadow="hover" :data-translation="word.translation" @click="subTransErrors"> <div class="icon-wrapper"><span class="sub-error-icon">‚ùå</span><span>Á∫†Èîô</span></div> </el-card>
                </el-col>
              </el-row>
            </el-space>
          </div>
          <el-button
            type="success"
            size="large"
            @click="subWords"
            >Ê∑ªÂä†ËØçÊù°</el-button
          >
          <el-button
            :disabled="getBtn.disabled"
            type="primary"
            @click.once="getPage"
            size="large"
            >{{ getBtn.text }}</el-button
          >
          <Question
            :words="data.words"
            v-if="showQues"
            v-loading="0"
            @closeQues="getPage"
          />
        </el-main>
      </el-container>
    </section>
  </div>
</template>

<script setup>
// ÂºïÂÖ•
import Question from "./Question";
import axios from "axios";
import qs from "qs";
import useDelayRef from "@/hooks/useDelayRef";
import addLikes from "@/hooks/useAddLikes"
import subError from '@/hooks/useSubError'
import subWords from "@/hooks/useSubWords";
import isLegal from '@/hooks/useIsLegal'
import { reactive, ref, watch } from "vue";
// ÂÆö‰πâÂèòÈáè

let showQues = ref(false);
let showLoading = ref(true);
let inputKeywords = useDelayRef("");
let showReturn = ref(false);
let getBtn = reactive({ text: "Ëé∑ÂèñÈóÆÂç∑", disabled: false });
let inputData = reactive({ text: inputKeywords, data: [] });
let getData = axios.create({
  timeout: 3000,
});

let data = reactive({
  // Âà§Êñ≠ÂûãÂèòÈáè
  isShowQuestion: false,
  isShowLoading: true,
  isShowReturn: false,
  // ÂØπË±°Êï∞ÁªÑÂûãÂèòÈáè
  getBtn: { text: "Ëé∑ÂèñÈóÆÂç∑", disabled: false },
  getTranslationsFromTemp: { word: inputKeywords },
  words: {},
});


// ÂÆö‰πâÁõëËßÜÂáΩÊï∞
watch(
  inputKeywords,
  (newValue) => {
    inputData.data.length = 0;
    if (newValue.match(/\S/) == null) {
      //ËæìÂÖ•Ê°Ü‰∏≠ÁöÑÂÄº‰∏∫Á©∫ÁöÑË°å‰∏∫
      showReturn.value = false;
    } else {
      //ËæìÂÖ•Ê°Ü‰∏≠ÁöÑÂÄº‰∏ç‰∏∫Á©∫ÁöÑË°å‰∏∫
      // ÊòæÁ§∫Âä†ËΩΩÂõæÊ†á
      showReturn.value = true;
      showLoading.value = true;
      $.ajax({
        url: "/api/getTranslationsFromPersistence",
        type: "POST",
        timeout: 1000,
        data: { word: newValue },
        dataType: "JSON",
        success: function (response) {
          showLoading.value = false;
          if (response.info) {
            inputData.data = [{ translation: "ÊöÇÊó†Êï∞ÊçÆ" }];
          } else {
            inputData.data = response;
          }
        },
        error: function () {
          showLoading.value = false;
          inputData.data = [{ translation: "ËØ∑Ê±ÇÂ§±Ë¥•" }];
        },
      })

      // axiosÂèëÈÄÅËØ∑Ê±Ç
      // getData.post('/post',inputData).then((response)=>{console.log(response.data)}).catch((error)=>{console.log(error)})
      // getData
      //   .post("/api/getTranslationsFromTemp", returnData,{headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
      //   .then(
      //     (response) => {
      //       console.log({word:newValue})
      //       showLoading.value = false;
      //       if (response.data.info) {
      //         inputData.data = [{ translation: "ÊöÇÊó†Êï∞ÊçÆ" }];
      //       } else {
      //         inputData.data = response.data;
      //       }
      //       console.log(response.data);
      //     },
      //   )
      //   .catch((error) => {
      //     alert("ÂèëÁîüÊú™Áü•ÈîôËØØ");
      //   });
    }
  },
  { immediate: true }
);

function getPage() {
  if (showQues.value == false) {
    getData
      .post("/api/getRandomQuestionnaire")
      .then((response) => {
        data.words = response.data;
        showQues.value = true;
        getBtn.text = "ÊàêÂäüÔºÅÔºÅ";
      })
      .catch((error) => {
        alert("ÂèëÁîüÈîôËØØ");
        getBtn.text = "Ëé∑ÂèñÂ§±Ë¥•";
        getBtn.disabled = true;
      });
  } else {
    showQues.value = false;
    getBtn.text = "‰Ω†Â∑≤Êèê‰∫§";
    getBtn.disabled = true;
  }
}

function addTransLikes(e) {
  let translation = e.target.parentNode.dataset.translation
  addLikes(translation)
}
function subTransErrors(e) {
  let translation = e.target.parentNode.dataset.translation
  let reason = prompt("ÈîôËØØÂéüÂõ†Ôºö")
  if (!isLegal(reason)) {
    alert("ÂÜÖÂÆπÊ†ºÂºèÈîôËØØ")
    return
  }
  subError(inputKeywords.value,translation,reason)
}
</script>

<style>
.main-wrapper {
  width: 800px;
  min-width: 200px;
}
#input {
  resize: none;
  position: relative;
  display: block;
  width: 100%;
  padding: 10px 11px;
  border: none;
  outline: none;
  box-sizing: border-box;
  border-radius: 5px;
  box-shadow: 0 0 0 1px rgb(163, 163, 163) inset;
  margin-bottom: 20px;
  font-family: Arial, Helvetica, sans-serif;
}
#input:focus {
  box-shadow: 0 0 0 1px #409eff inset;
}
#input::-webkit-scrollbar {
  display: none;
}
.show-box {
  margin-bottom: 30px;
  width: 100%;
  height: auto;
  min-height: 100px;
}
.translation-add-row {
  display: flex;
  justify-content: space-between;
}
.translation-word-title {
  font-size: large;
  font-weight: bold;
}
.single-translation .el-card__body {
  height: 25px;
  line-height: 25px;
  padding: 20px 0;
}
.translation-icon {
  cursor: pointer;
  font-size: small;
}
.sub-error-icon,.add-like-icon {
  display: block;
}
@media screen and (max-width:960px) {
  .single-translation .el-card__body {
  height: 15px;
  line-height: 15px;
}
}
.translation-add .el-card__body {
  line-height: 5px;
}
.translation-icon,.translation-content {
  height: 100%;
}
.translation-icon .el-card__body {
  padding: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.el-card.is-hover-shadow {
  border: solid 1px black;
}
.icon-wrapper {
  pointer-events: none;
}
@media screen and (max-width: 960px) {
  .main-wrapper {
    width: 100%;
  }
}
</style>