import { customRef } from 'vue'

export default function useDelayRef(value) {
    return customRef((track, trigger) => {
        let timer;
        return {
            get() {
                // console.log(`‰ªémyRefËøô‰∏™ÂÆπÂô®ËØªÂèñÊï∞ÊçÆ,data:${value}`);
                track(); //ÈÄöÁü•ËøΩË∏™valueÁöÑÂèòÂåñ(Ë∑ügetterÂïÜÈáè‰∏Ä‰∏ãËÆ©ÂÆÉÊòéÁ°Æ‰Ω†Ëøô‰∏™valueÊòØÊúâÁî®ÁöÑ)
                return value; //ËØªÂèñÁöÑÊó∂ÂÄôÂ∞±‰ºöË∞ÉÁî®get
            },
            set(nv) {
                // console.log(`myRefÂÆπÂô®‰∏≠ÁöÑÊï∞ÊçÆË¢´‰øÆÊîπ,dataÊîπ‰∏∫${nv}`);
                clearTimeout(timer);
                timer = setTimeout(() => {
                    value = nv;
                    trigger(); //triggerÈÄöÁü•vueÈáçÊñ∞Ëß£ÊûêÊ®°Áâà //Èò≤Êäñ
                }, 500);
            }
        }
    })
}